<template>
  <div class="admin-container">
    <h1>&#x1F3AD; Contest Admin</h1>

    <div class="card black">
      <label>Costume name</label>
      <input v-model="name" placeholder="e.g. Pirate" />

      <label>Category</label>
      <select v-model="category">
        <option :value="1">Lustigstes Kostüm</option>
        <option :value="2">Bestes Gruppenkostüm</option>
        <option :value="3">Kreativstes Kostüm</option>
      </select>

      <label>Image</label>
      <input type="file" accept="image/*" @change="onFileChange" />

      <img v-if="preview" :src="preview" class="preview" />

      <button
        class="primary"
        :disabled="!canSubmit"
        @click="addCostume"
      >
        Add costume
      </button>
    </div>

    <div class="card danger">
      <button class="danger-btn" @click="conclude">
        &#x274C; Conclude contest
      </button>
    </div>

    <div class="card">
      <button class="reset-btn" @click="resetContest">
        &#x1F504; Reset contest votes
      </button>
    </div>

    <div class="card vote-card">
      <div class="vote-summary">
        <div class="vote-total">
          <div class="vote-label">Total votes cast</div>
          <div class="vote-value">{{ totalVotesAll }}</div>
        </div>

        <div class="vote-breakdown">
          <div
            v-for="cat in [1, 2, 3]"
            :key="cat"
            class="vote-row"
          >
            <span class="vote-cat">{{ categoryLabel(cat) }}</span>
            <span class="vote-count">{{ totalVotesByCategory(cat) }}</span>
          </div>
        </div>
      </div>
    </div>

    <div class="card">
      <h2>Preview</h2>

      <div
        v-for="cat in [1,2,3]"
        :key="cat"
        class="preview-category"
      >
        <h3>{{ categoryLabel(cat) }}</h3>
        <ul>
          <li
            v-for="c in store.getCostumesByCategory(cat)"
            :key="c.id"
          >
            {{ c.name }} ({{ c.votes }} votes)
          </li>
        </ul>
      </div>
    </div>

    <div class="card leaderboard">
      <h2>Ranking</h2>

      <div
        v-for="category in [1, 2, 3]"
        :key="category"
        class="category-card"
      >
        <h3>{{ categoryLabel(category) }}</h3>

        <div
          v-if="results(category).length === 0"
          class="empty"
        >
          No entries
        </div>

        <ol v-else class="ranking">
          <li
            v-for="(costume, index) in results(category)"
            :key="costume.id"
            class="rank-item"
          >
            <span class="rank">
              {{ medal(index) }}
            </span>

            <img
              :src="costume.image"
              class="thumb"
              alt="Costume"
            />

            <div class="info">
              <div class="name">{{ costume.name }}</div>
              <div class="votes">{{ costume.votes }} votes</div>
            </div>
          </li>
        </ol>
      </div>
    </div>

    <div class="card all-costumes">
      <h2>All Costumes</h2>

      <div v-if="allCostumes.length === 0" class="empty">
        No entries
      </div>

      <table v-else class="costume-table">
        <thead>
          <tr>
            <th>Image</th>
            <th>Name</th>
            <th>Category</th>
            <th>Votes</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="costume in allCostumes" :key="costume.id">
            <td>
              <img :src="costume.image" alt="Costume" class="table-thumb" />
            </td>
            <td>{{ costume.name }}</td>
            <td>{{ categoryLabel(costume.category) }}</td>
            <td>{{ costume.votes }}</td>
            <td>
              <button class="delete-btn" @click="removeCostume(costume.id)">
                &#x274C;
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { useCompleteStore } from '@/store/store';

export default {
  name: 'AdminView',

  data() {
    return {
      name: '',
      category: 1,
      image: null,
      preview: null
    };
  },

  computed: {
    store() {
      return useCompleteStore();
    },
    canSubmit() {
      return this.name && this.image && !this.store.isContestConcluded;
    },
    allCostumes() {
      return [1, 2, 3].flatMap((category) =>
        this.store.getCostumesByCategory(category).map((c) => ({
          ...c,
          category
        }))
      );
    },
    totalVotesAll() {
      return [1, 2, 3].reduce(
        (sum, category) => sum + this.totalVotesByCategory(category),
        0
      );
    }
  },

  mounted() {
    this.store.loadContestFromFirebase();
  },

  methods: {
    onFileChange(e) {
      const file = e.target.files[0];
      if (!file) return;

      const img = new Image();
      const reader = new FileReader();

      reader.onload = (event) => {
        img.src = event.target.result;
      };

      img.onload = () => {
        const MAX_WIDTH = 800;
        const scale = MAX_WIDTH / img.width;

        const canvas = document.createElement('canvas');
        canvas.width = MAX_WIDTH;
        canvas.height = img.height * scale;

        const ctx = canvas.getContext('2d');
        ctx.drawImage(img, 0, 0, canvas.width, canvas.height);

        const compressedBase64 = canvas.toDataURL('image/jpeg', 0.7);

        this.image = compressedBase64;
        this.preview = compressedBase64;
      };

      reader.readAsDataURL(file);
    },

    async addCostume() {
      if (!this.canSubmit) return;

      await this.store.addCostume({
        category: this.category,
        name: this.name,
        image: this.image
      });

      this.name = '';
      this.category = 1;
      this.image = null;
      this.preview = null;
    },

    async conclude() {
      if (!confirm('This will stop all voting. Continue?')) return;
      await this.store.concludeContest();
    },

    async resetContest() {
      if (!confirm('Reset all votes and allow voting again?')) return;
      await this.store.resetContest();
    },

    async removeCostume(costumeId) {
      if (!confirm('Remove this costume?')) return;
      await this.store.deleteCostume(costumeId);
    },

    results(category) {
      return this.store.getRankedResults(category);
    },

    categoryLabel(category) {
      if (category === 1) return 'Lustigstes Kostüm';
      if (category === 2) return 'Bestes Gruppenkostüm';
      if (category === 3) return 'Kreativstes Kostüm';
      return `Category ${category}`;
    },
    totalVotesByCategory(category) {
      return this.store
        .getCostumesByCategory(category)
        .reduce((sum, costume) => sum + (costume.votes || 0), 0);
    },

    medal(index) {
      if (index === 0) return '\uD83E\uDD47';
      if (index === 1) return '\uD83E\uDD48';
      if (index === 2) return '\uD83E\uDD49';
      return `#${index + 1}`;
    }
  }
};
</script>

